import { Injectable } from '@angular/core';
import { CanActivate, Router} from '@angular/router';
import { isLoggedin } from './is-loggedin';
import { SubDomainService,CookieService } from './../services/index';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(
    private router: Router,
    private subDomainService:SubDomainService,
    private _cookieService:CookieService
  ) {}

  canActivate() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let sub_domain = this.subDomainService.subDomain.sub_domain;
    if (this._cookieService.readCookie('storage') != null && storage.user.role === "ADMIN") { return true; }
    if (this._cookieService.readCookie('storage') != null && storage.companyList.indexOf(sub_domain) >= 0) { return true; }
    this.router.navigate(['/login']);
    return false;
  }
}



