import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { SubDomainService, CookieService } from './../services/index';

@Injectable()
export class FreemiumGuard implements CanActivate {
    constructor(
        private router: Router,
        private subDomainService: SubDomainService,
        private _cookieService: CookieService
    ) { }

    canActivate() {
        let storage = JSON.parse(this._cookieService.readCookie('storage'));
        let sub_domain = this.subDomainService.subDomain.sub_domain;
        let plan_id = storage.company.billing.chargebee_plan_id;
        let companyAccess = JSON.parse(this._cookieService.readCookie('filepicker_token_json'));
        let subscription_status = '';
        if (!companyAccess)
            window.location.href = window.location.origin + '/logout';
        else
            companyAccess.forEach((e: any) => {
                if (e.key === sub_domain) {
                    subscription_status = e.value;
                }
            });
        // after initial  trial period if customer dont upgrade the plan
        if (((subscription_status == "active" && plan_id == 'freemium') || subscription_status == "cancelled" ) && !storage.company.is_admin_created) {
            let timer = setInterval(()=>{
                storage = JSON.parse(this._cookieService.readCookie('storage'));
                if(storage){
                    this.abortTimer(timer); 
                }
            }, 200);

            return false;
        }

        return true;


    }

    abortTimer(timer : any) { 
         clearInterval(timer);
         this.router.navigate(['/settings/membership']);
    }
}



