import {ModuleWithProviders, NgModule, Optional, SkipSelf }     from '@angular/core';
import { CookieService, LoggedInService, MembershipService, SubDomainService, CompanyService, UserService, SettingsCommunicationService, Script, DashboardService} from './shared/services/index';
import {SharedModule} from './shared/modules/shared.module';
import { CalculatorAnalytics } from './site/components/+analytics/services/calculator-analytics.service';
import {FeatureAuthService} from './shared/services/feature-access.service';

@NgModule({
    exports: [SharedModule]
})

export class CoreModule {

    static forRoot(): ModuleWithProviders {
        return {
            ngModule: CoreModule,
            providers: [UserService, LoggedInService, SettingsCommunicationService, MembershipService, CompanyService, CookieService, Script, SubDomainService, FeatureAuthService]
        };
    }

    constructor( @Optional() @SkipSelf() parentModule: CoreModule) {
        if (parentModule) {
            throw new Error('CoreModule is already loaded. Import it in the AppModule only');
        }
    }
}



