import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { SubDomain } from './../shared/interfaces/subdomain.interface';
import { SubDomainService, CookieService, FeatureAuthService } from '../shared/services/index';
import { environment } from '../../environments/environment';
import { Script } from '../shared/services/script.service';
import { MarketingService } from '../shared/services/marketing.service';
import { Title } from '@angular/platform-browser';
import {FeatureAccess} from '../shared/interfaces/features.interface';

declare var jQuery: any;
declare var window: any;

declare var LeadDyno: any;
declare var webengage: any;
declare var Appcues: any;

@Component({
  selector: 'og-site',
  template: `
    <sd-toolbar></sd-toolbar>
    <router-outlet></router-outlet>
  `,
  styleUrls: ['./site.component.css']
})

export class SiteComponent implements OnInit, OnDestroy {
  subDomain: SubDomain;
  intercom_id: string = environment.INTERCOM_ID;
  constructor(private subDomainService: SubDomainService,
    private _cookieService: CookieService,
    private _featureAuthService: FeatureAuthService,
    private router: Router,
    private _script: Script,
    private _marketingService: MarketingService,
    private titleService: Title
  ) {
    this.titleService.setTitle("Outgrow Home");
    this.subDomain = subDomainService.subDomain;
    this._script.load('marketing')
      .then((data) => {
        //console.log('Scripts Loaded', data);
        if (data.length && data[0].status == 'Loaded')
          _marketingService.initMarketingStuff();
      })
      .catch((error) => {
        //any error
      });
  }

  ngOnInit() {
    //Intercom
  }

  ngOnDestroy() {
    window.Intercom('hide');
    window.Intercom('shutdown');
  }
}



