import { Component, OnInit, OnDestroy } from '@angular/core';
import { SafeResourceUrl, DomSanitizer} from '@angular/platform-browser';
import { environment } from './../environments/environment';
import { SubDomain } from './shared/interfaces/subdomain.interface';
import { CookieService, SubDomainService, FeatureAuthService } from './shared/services/index';
import { Script } from './shared/services/index';
import {FeatureAccess} from './shared/interfaces/features.interface';
declare var window: any;
declare var jQuery: any;

@Component({
  selector: 'sd-app',
  templateUrl: './app.component.html',
})

export class AppComponent implements OnInit, OnDestroy {

  subDomain: SubDomain;
  safeSubDomainUrl: SafeResourceUrl = '';
  subDomainUrl: string = '';
  mainUrl: SafeResourceUrl = '';
  sub_domain: any = '';
  constructor(
    private subDomainService: SubDomainService,
    private featureAuthService: FeatureAuthService,
    sanitizer: DomSanitizer,
    private _cookieService: CookieService,

  ) {
    this.subDomain = subDomainService.subDomain;
    this.mainUrl = sanitizer.bypassSecurityTrustResourceUrl(environment.PROTOCOL + environment.PARENT_APP_DOMAIN + '/cross-domain.html');
    this.subDomainService.subDomainExists();
    if (this.subDomain.is_sub_domain_url) {
      this.subDomainUrl = this.subDomain.sub_domain + '.' + environment.APP_EXTENSION;
      this.safeSubDomainUrl = sanitizer.bypassSecurityTrustResourceUrl(
        environment.PROTOCOL + this.subDomain.sub_domain + '.' + environment.APP_EXTENSION
      );
    }
    window.onbeforeunload = function (e: any) {
      if (localStorage.getItem('targetRoute') && _cookieService.readCookie('storage') == null)
        localStorage.clear();
    };
  }

  ngOnInit() {
    //Intercom
    let exceptRoute : Boolean = !this.exceptionRoutes();
    if (this._cookieService.readCookie('storage') && exceptRoute) {
      let storage = JSON.parse(this._cookieService.readCookie('storage'));
      if(storage.user.role === 'USER'){
        	this.featureAuthService.getAllFeatureAccess().subscribe((result) => {
    	      this.featureAuthService.features = new FeatureAccess(result);
    	    });
      }
		
		
      
    }

    // Google remarketing tag
    // let urla = window.location.href;
    // if(urla.indexOf("outgrow.co") >= 0) {
    //     window.google_trackConversion({
    //         google_conversion_id: 876549213,
    //         google_custom_params: window.google_tag_params,
    //         google_remarketing_only: true
    //     });
    // }

    /** changes for meta tags */
    jQuery('meta[property="og:description"]').attr('content', 'venturepact');
    jQuery('meta[property="og:title"]').attr('content', 'venturepact');
    let url = window.location.href;
    let routeObject = url.split('/');
    url = window.location.hostname;
    this.sub_domain = url.split('.')[0];
    if (this._cookieService.readCookie('storage')) {
      let storage = JSON.parse(this._cookieService.readCookie('storage'));
      if (!this.subDomain.is_sub_domain_url && storage.user.role !== 'ADMIN' && routeObject[3] !== 'verifyEmail') {
        // console.log('this is the culprit here');
        this.redirectToFirstCompany();
      }
      else if (!this.subDomain.is_sub_domain_url && storage.user.role === 'ADMIN' && routeObject[3] !== 'admin') {
        // console.log('now this is the culprit here');
        window.location.href = window.location.origin + '/admin';
      }
      else if (this.subDomain.is_sub_domain_url && !routeObject[3]) {
        this.redirectToDashboard();
      }
      //Appcues Identify
      // Appcues.identify(storage.user._id, {
      //   name: storage.user.name,
      //   email: storage.user.emails[0].email,
      //   created_at: (Math.round(Date.parse(storage.user.createdAt) / 1000))
      // });
    } else if (!routeObject[3] && this.subDomain.is_sub_domain_url) {
      //Appcues Anonymous
      // Appcues.anonymous();
      url = 'app.' + environment.APP_EXTENSION + '/login';
      window.location.href = environment.PROTOCOL + url;
    }
  }

  redirectToDashboard() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let companyAccess = JSON.parse(this._cookieService.readCookie('filepicker_token_json'));
    let url = '';
    companyAccess.forEach((e: any) => {
      if (e.key === this.sub_domain) {
        url = this.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard';
      }
    });
    let company = storage.company;
    if (url == '')
      url = company.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard';
     let isException = this.exceptionRoutes();
     if (isException){
       return;
     }
      window.location.href = environment.PROTOCOL + url;
  }

  redirectToFirstCompany() {
    let storage = JSON.parse(this._cookieService.readCookie('storage'));
    let company = storage.company;
    let url = company.sub_domain + '.' + environment.APP_EXTENSION + '/dashboard';
    let isException = this.exceptionRoutes();
     if (isException) {
       return;
     }
      window.location.href = environment.PROTOCOL + url;
  }

  exceptionRoutes() {
    let url = window.location.pathname;
    if( url === '/authorize/salesforce') {
      return true;
    }
    if( url === '/authorize/aweber') {
      return true;
    }
    return false;
  }


  // updateBillingStatusCookie(){
  //   this._userService.updatebillingStatus().
  //     subscribe((result:any)=>{
  //       let statusCookie = result;
  //       this._cookieService.createCookie('filepicker_token_json', JSON.stringify(statusCookie),3);
  //       console.log(JSON.parse(this._cookieService.readCookie('filepicker_token_json')));
  //       this.featureAuthService.getAllFeatureAccess();
  //   });
  // }

  ngOnDestroy() {
    window.Intercom('hide');
    window.Intercom('shutdown');
    if (window.location.href.indexOf('/preview') >= 0) {
      window.Intercom('update', { hide_default_launcher: false });
    }

  }
}




