import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoggedIn } from './../../shared/interfaces/logged-in.interface';
import { LoggedInService } from './../../shared/services/logged-in.service';
import { SubDomain } from './../../shared/interfaces/subdomain.interface';
import { SubDomainService } from './../../shared/services/subdomain.service';
@Component({
  selector: 'app-home',
  template: `
    <signup-component *ngIf="!subDomain.is_sub_domain_url && !loggedIn.isLoggedIn"></signup-component>
  `
  //<company-home *ngIf="subDomain.is_sub_domain_url || subDomain.exists"></company-home>
})

export class HomeComponent implements OnInit {

  subDomain: SubDomain;
  loggedIn: LoggedIn;

  constructor(private subDomainService: SubDomainService, 
    private loggedInService: LoggedInService, 
    private router : Router) {
    this.subDomain = subDomainService.subDomain;
    this.loggedIn = loggedInService.loggedIn;
    // if(
    //     this.subDomain.is_sub_domain_url &&
    //     !this.subDomain.exists &&
    //     this.subDomain.sub_domain !== ''
    //   ) {
    //       this.router.navigate(['/Error']);
    //     }
  }

  ngOnInit() {
    if(this.loggedIn.isLoggedIn)
      this.router.navigate(['/dashboard']);
  }

}



